import React, { Fragment, useEffect, useState } from "react";
import axios from "axios";
import { useFormik } from "formik";

import { admSchema } from '../../../yupSchema/admSchema';
import environment from "../../../environment";
import { dateSchema } from "../../../yupSchema/dateSchema";
import { infraSchema } from "../../../yupSchema/infraSchema";
import { newsSchema } from "../../../yupSchema/newsSchema";
import AdmCard from "../../containers/administration/card/AdmCard";
import InfraCard from "../../containers/infra/card/InfraCard";
import NewsCard from "../../containers/news/card/NewsCard";
import DatesCard from "../../containers/dates/card/DatesCard";
import "./AdminForm.css";


const AdminForm= ({ pageType, formType}) => {
  const [successMessage, setSuccessMessage] = useState({message: ""})

 const [isCreate, setCreate] = useState(false);
 const [isEdit, setEdit] = useState(false);
 const [dataWithId, setDataWithId] = useState("")
  const [isDate, setDate] = useState(false);
  const [isAdm, setAdm] = useState(false)
  const [isInfra, setInfra] = useState(false);
  const [isNews, setNews] = useState(false);
  const baseUrl = environment.baseUrl;
  const [file, setFile] = useState(null)
  function addImage(e){
    const file = e.target.files[0];
    console.log(file)
    setFile(file)
  }
  let title;
  let initialValues;
  let url = `${baseUrl}/api/${pageType}/create-new`;
  if(isEdit) url = `${baseUrl}/api/${pageType}/update/${dataWithId._id}`;
  let schema;
  if(pageType==="adm"){
    title = "Administration"
    initialValues ={ author:dataWithId?.author || "", degree:dataWithId?.degree || "",
     experience: dataWithId?.experience || "" };
    schema = admSchema;
  }

  if(pageType==="infra"){
    title = "Infrastucture and Facility"
    initialValues ={ title:dataWithId?.title || "", content:dataWithId?.content || "" };
    schema = infraSchema;
  }
  if(pageType==="dates"){
   title = "New Dates"
    initialValues ={date: dataWithId?.date, title:dataWithId?.title || "", content:dataWithId?.content ||"" };
    schema = dateSchema;
  }
  if(pageType==="news"){
    title = "All news"
    initialValues ={ title:dataWithId?.title || "", content:dataWithId?.content };
    schema = newsSchema;
  }

  

  let getUrl = `${baseUrl}/api/${pageType}/all`;

    const [datas, setData] = useState([])
    useEffect(()=>{
      if(pageType ==="dates"){
        setDate(true)
        }else if(pageType ==="adm"){
          setAdm(true);
        }else if(pageType ==="infra"){
          setInfra(true)
        }else if(pageType ==="news"){
          setNews(true)
        }

        axios.get(getUrl).then(resp=>{
            console.log("got data", resp)
            setData(resp.data.data)
             console.log(datas)
        }).catch(e=>{ console.log("Error ", e)})

    },[successMessage])

    const delHandle =(id)=>{
     if(window.confirm("Are you sure you want to delete?")===true){
      console.log(id,"delete Id")
      const deleteUrl = `${baseUrl}/api/${pageType}/delete/${id}`;
      axios.get(deleteUrl).then((resp)=>{
        console.log("Deleted")
          if(resp.data.success){
            setSuccessMessage({message: resp.data.message})
          }
      }).catch((e)=>{
        console.log("Error", e)
      })
    }
    }



    const editForm = (id)=>{
      setCreate(true);
      setEdit(true);
    const data = datas.filter(x=>x._id===id);
     setDataWithId(data[0])
    }



  const Formik = useFormik({
    enableReinitialize:true,
    initialValues: initialValues,
    validationSchema: schema,
    onSubmit: (values) => {
      const fd = new FormData();
      if(pageType !="dates"  && !isEdit){ fd.append("image", file, file.name);  }
      if(pageType==="dates"){  fd.append("date", values.date)}
      if(pageType ==="adm"){
        fd.append("author", values.author);
        fd.append("degree", values.degree);
        fd.append("experience", values.experience)
      }else{
        fd.append("title", values.title);
        fd.append("content", values.content)
      }
      
     if(isEdit){
      console.log(fd)
      axios.patch(url, fd)
      .then((resp) => {
        if(resp.data.success){
         setSuccessMessage({message: resp.data.message})
         console.log(successMessage, "Successs MEssaage");
         setCreate(false)
        }
        Formik.resetForm();
      })
      .catch((err) => {
        console.log(err, "error");
      });
     }else{
      axios.post(url, fd)
        .then((resp) => {
          if(resp.data.success){
           setSuccessMessage({message: resp.data.message})
           console.log(successMessage, "Successs MEssaage");
           setCreate(false)
          }
          Formik.resetForm();
        })
        .catch((err) => {
          console.log(err, "error");
        });
    }
     }
  

  });


 





  return (
    <Fragment>
      <h2 className="text-center bg-white"><span className="text-white bg-info">Admin</span>{title}</h2>
      {isCreate?(
              <div className='w-100 bg-primary position-relative'>
          <button style={{zIndex:"100", right:"0"}} className='btn btn-sm btn-danger position-absolute d-flex' onClick={()=>{setCreate(false)}}>X</button>
    
      <div className="container-form-registration 
      m-auto wi-100">       
          <form onSubmit={Formik.handleSubmit} style={{zIndex:"99"}} className="w-100 p-5 d-flex flex-column bg-white position-absolute w-75 " method="POST">
           
           {isAdm?(<div className="mb-3">
              <label  className="form-label">
                Author
              </label>
              <input type="text" name="author" className="form-control"
                value={Formik.values.author}
                 onChange={Formik.handleChange}
                 onBlur={Formik.handleBlur}
                placeholder="Author *"
              />
            { Formik.touched.author && Formik.errors.author ?( <p className='form-error'>
              {Formik.errors.author}</p>):null }
            </div>
           ):(
            <div className="mb-3">
              <label  className="form-label">
                Title
              </label>
              <input type="text" name="title" className="form-control"
                value={Formik.values.title}
                 onChange={Formik.handleChange}
                 onBlur={Formik.handleBlur}
                placeholder="Title *"
              />
            { Formik.touched.title && Formik.errors.title ?( <p className='form-error'>
              {Formik.errors.title}</p>):null }
            </div>
           
           )}
            
           {isDate?(
            <div className="mb-3">
            <label  className="form-label">
              Date
            </label>
            <input type="date" name="date" className="form-control"
                 onChange={Formik.handleChange}
                 onBlur={Formik.handleBlur}
              placeholder="Image"
            />
        </div>
           ):(  
             <div className="mb-3">
            {isEdit?( 
              <img src={require(`../../../assets/upload/${dataWithId?.imgUrl}`)}
               height='80px' alt={"Image"}/>):(
                <>
                <label  className="form-label">
                Image
              </label>
              <input type="file" name="image" className="form-control"
               onChange={(event)=>{ addImage(event)}}
                placeholder="Image"
              />
              </>
             )}

          </div>)}
         

         {isAdm?(<div className="mb-3">
              <label  className="form-label">
                Degree
              </label>
              <input type="text" name="degree" className="form-control"
                value={Formik.values.degree}
                 onChange={Formik.handleChange}
                 onBlur={Formik.handleBlur}
                placeholder="Degree *"
              />
            { Formik.touched.degree && Formik.errors.degree ?( <p className='form-error'>
              {Formik.errors.degree}</p>):null }
            </div>
           ):(
            <div className="mb-3">
            <label  className="form-label">
              Content
            </label>
            <textarea
              type="text"
              name="content"
              className="form-control"
              value={Formik.values.content}
              onChange={Formik.handleChange}
              onBlur={Formik.handleBlur}
              placeholder="Content *"
            ></textarea>
                 { Formik.touched.content && Formik.errors.content ?( <p className='form-error'>{Formik.errors.content}</p>):null }
          </div>
           )}


{isAdm?(<div className="mb-3">
              <label  className="form-label">
             Experience 
              </label>
              <input type="number" name="experience" className="form-control"
                value={Formik.values.experience}
                 onChange={Formik.handleChange}
                 onBlur={Formik.handleBlur}
                placeholder="Experience *"
              />
            { Formik.touched.experience && Formik.errors.experience ?( <p className='form-error'>
              {Formik.errors.experience}</p>):null }
            </div>
           ):(null)}

            <button type="submit" className="btn btn-dark">
                  Submit
            </button>
          </form>
    
      </div>
      </div>
      ):null}
      <button onClick={()=>{setCreate(true) }}>Add New</button>
    
 {isAdm && datas && datas.map((data)=>{ return (
           <AdmCard data={data} key={data._id} del={ delHandle} edit={editForm}/>
         )})}

{isInfra && datas && datas.map((data)=>{ return (
          //  <Card data={data} del={ delHandle}/>
          <InfraCard data={data} key={data._id} del={delHandle} edit={editForm}/>
         )})}

{isNews && datas && datas.map((data)=>{ return (
          //  <Card data={data} del={ delHandle}/>
          <NewsCard data={data} key={data._id} del={delHandle} edit={editForm} />
         )})}

{isDate && datas && datas.map((data)=>{ return (
          //  <Card data={data} del={ delHandle}/>
          <DatesCard data={data} key={data._id} del={delHandle} edit={editForm} />
         )})}



    </Fragment>
  );
};

export default AdminForm;

