import React from 'react'
import { GiTrophyCup } from 'react-icons/gi';
import Parallaxdata from "../../../data/home/parallax.json"
import './Parallax.css'

function Parallax() {

  return (
    <>
     <div className='row justify-content-center align-items-center mt-5'>
        <div className='col-10 parllax '>
            <div className='row justify-content-center align-items-center m-5 p-4'>
            {Parallaxdata &&
            Parallaxdata.map((single, key) => {
                return (
                    <div className='col-lg-3 d-flex justify-content-center' key={key}>
                    <div className='parallax-icon'>
                        <GiTrophyCup/> 
                    </div>
                    <div className='ml-2'>
                    <p className='parallax-number'>{single.number}</p>
                        <p className='parallax-text'>{single.name}</p>
                   
                    </div>
                  
                </div>
                );
              })}


            </div>
        </div>
    </div>
    </>
  )}
  export default Parallax